.wp-block-zolo-cta {

    --zolo-item-padding: clamp(2.5rem, 5vw, 3.75rem);
    --zolo-title-margin: 0 0 20px;
    --zolo-title-text-color: #212126;
    --zolo-desc-text-color: #71737c;
    --zolo-desc-margin: 0;
    --zolo-content-margin: 0 0 25px;
    --zolo-button-padding: 14px 30px;
    --zolo-button-gap: 7px;
    --zolo-button-border-width: 2px;
    --zolo-button-border-style: solid;
    --zolo-button-icon-size: 16px;
    --zolo-item-gap: 20px;
    --zolo-group-button-gap: 10px;

    padding: var(--zolo-item-padding);
    border-radius: var(--zolo-item-border-radius);
    background-color: var(--zoloblocks-item-bg-color);
    border-width: var(--zolo-item-border-width);
    border-style: var(--zolo-item-border-style);
    border-color: var(--zoloblocks-item-border-color);
    box-sizing: border-box;
    text-align: center;
    // overflow: hidden;
    .zolo-call-out__title {
        margin: var(--zolo-title-margin);
        font-size: 32px;
        color: var(--zolo-title-text-color);
    }
    .zolo-call-out__text {
        color: var(--zolo-desc-text-color);
        margin: var(--zolo-desc-margin);
        font-size: 16px;
        font-weight: 400;
    }

    .zolo-call-out__content {
        margin: var(--zolo-content-margin);
        // width: 100%;
    }

    .zolo-button {
        padding: var(--zolo-button-padding);
        background-color: var(--zoloblocks-brand-color);
        text-decoration: none;
        display: inline-flex;
        gap: var(--zolo-button-gap);
        align-items: center;
        transition: all 0.3s ease;
        border-radius: var(--zoloblocks-button-border-radius);
        color: var(--zoloblocks-brand-button-color);
        font-weight: 500;
        border-width: var(--zolo-button-border-width);
        border-style: var(--zolo-button-border-style) ;
        border-color: var(--zoloblocks-brand-color);
        &:hover {
            color: var(--zoloblocks-brand-text-color);
            background-color: transparent;
            svg {
                fill: var(--zoloblocks-brand-text-color);
            }
        }
        svg {
            width: var(--zolo-button-icon-size);
            height: var(--zolo-button-icon-size);
            fill: var(--zoloblocks-brand-button-color);
            display: block;
        }

        &.secondary {
            background-color: transparent;
            color: var(--zoloblocks-brand-color);
            svg {
                fill: var(--zoloblocks-brand-color);
            }

            &:hover {
                color: var(--zoloblocks-brand-button-color);
                background-color: var(--zoloblocks-brand-color);
                svg {
                    fill: var(--zoloblocks-brand-button-color);
                }
            }
        }
    }

    .zolo-call-out__icon-left {
        .zolo-button {
            flex-direction: row-reverse;
        }
    }

    .zolo-call-out {
        &.style-1 {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            gap: var(--zolo-item-gap);

            .zolo-call-out__buttons {
                width: 100%;
                text-align: right;
                max-width: fit-content;
            }
            .zolo-call-out__content {
                --zolo-content-margin: 0;
                margin: var(--zolo-content-margin);
            }
        }
        &.reserve-position {
            &.style-1 {
                flex-direction: row-reverse;
            }
        }
    }

    .zolo-call-out__buttons {
        display: inline-block;
    }
    .zolo-call-out-btns-group {
        display: flex;
        justify-self: center;
        flex-wrap: wrap;
        gap: var(--zolo-group-button-gap);
    }
}

@media (min-width: 768px) {
    .wp-block-zolo-cta {
        .zolo-call-out {
            &.style-1 {
                flex-direction: row;
                align-items: center;
            }
        }
    }
}
